//Use mui Modal

import { Button, TextField } from "@mui/material";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import Typography from "@mui/material/Typography";
import * as React from "react";
import { useState } from "react";
import { Block } from "types/landProps";
import styles from "./Modal.module.css";
import useModal from "./useModal";

interface BasicModalProps {
  showModal: boolean;
  isLoggedIn: boolean;
  setShowModal: any;
  block: Block;
  canvasRef: any;
}

export default function BasicModal({
  showModal,
  setShowModal,
  block,
  isLoggedIn,
  canvasRef,
}: BasicModalProps) {
  const { writeSubmissionFromModal } = useModal(canvasRef);
  const [modalSubmission, setModalSubmission] = useState("");

  const handleOpenClose = () => {
    setShowModal(!showModal);
  };

  function onClickModalSubmit(e: any) {
    e.preventDefault();
    console.log({ modalSubmission });
    writeSubmissionFromModal(block, modalSubmission);
    setModalSubmission("");
    handleOpenClose();
  }
  return (
    <React.Fragment>
      <Modal
        open={showModal}
        onClose={() => {
          handleOpenClose();
          setModalSubmission("");
        }}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        {isLoggedIn ? (
          <Box className={styles.modalBox}>
            <Typography id="modal-modal-title" variant="h6" component="h2">
              Block Id is {block.id}
            </Typography>
            <Typography id="modal-modal-description" sx={{ mt: 2 }}>
              Type now!!!!!!!!!
            </Typography>
            <form onSubmit={onClickModalSubmit} className={styles.modalInput}>
              <TextField
                id="modal-modal-input"
                label="whatever"
                autoFocus
                variant="standard"
                value={modalSubmission}
                onChange={event => {
                  setModalSubmission(event.target.value);
                }}
              />
              <Button type="submit" variant="contained">
                Submit
              </Button>
            </form>
          </Box>
        ) : (
          <Box className={styles.modalBox}>
            <Typography id="modal-modal-title" variant="h6" component="h2">
              You need to sign in
            </Typography>
            <Typography id="modal-modal-description" sx={{ mt: 2 }}>
              Please connect to MetaMask
            </Typography>
          </Box>
        )}
      </Modal>
    </React.Fragment>
  );
}
