import { useEffect, useState } from "react";
import contractService from "services/contract.service";
import { Block } from "types/landProps";

export default function useModal(canvasRef: any) {
  const [contextState, setContextState] =
    useState<CanvasRenderingContext2D | null>();

  async function writeSubmissionFromModal(block: Block, submission: string) {
    try {
      const isWritten = await contractService.writeOnNewsLand(
        block.id,
        submission
      );
      if (isWritten) {
        contextState!.textBaseline = "middle";
        contextState!.fillText(
          submission,
          block.attribute.startX,
          block.attribute.startY + 15
        );
      alert("SUCCESS!!");

      }
    } catch (err) {
      alert("FAIL!!")
      console.error("ERROR!!! - FAILED TO WRITE!!!!");
    }
  }

  useEffect(() => {
    const canvas = canvasRef.current!;
    const context: CanvasRenderingContext2D = canvas.getContext("2d");
    setContextState(context);

    if (!canvas || !context) {
      console.error("canvas is not exist");
      return;
    }
  }, []);

  return {
    writeSubmissionFromModal,
  };
}
