import { ethers } from "ethers";
import { address } from "../utils/ContractInfo";
import abi from "utils/LandPortal.json";

declare var window: any;

const contractAddress = address;
const contractABI = abi.abi;

const getContentByLandId = async (landId: number) => {
  try {
    const { ethereum } = window;

    if (ethereum) {
      const provider = new ethers.providers.Web3Provider(ethereum);
      const signer = provider.getSigner();
      const landPortalContract = new ethers.Contract(
        contractAddress,
        contractABI,
        signer
      );

      let result = await landPortalContract.getContentById(landId);

      return result;

      // await signer.sendTransaction({
      //   to: contractAddress,
      //   value: ethers.utils.parseEther("0.01"),
      //   gasPrice: 8000000000,
      // });

      // let result = await newsLandPortalContract.openQuestion(
      //   id,
      //   BigInt(reward * 1000000000 * 1000000000),
      //   {
      //     value: ethers.utils.parseEther(`${reward}`),
      //   }
      // ); // 0.01 ether
      // await result.wait(); // waiting till mine complete
      // let after = await newsLandPortalContract.getQuestionById(id);
    } else {
      console.log("Ethereum object doesn't exist!");
      throw new Error("Metamask is needed");
    }
  } catch (error) {
    console.error(error);
  }
};

const writeOnNewsLand = async (landId: number, content: string) => {
  try {
    console.log("start");
    const { ethereum } = window;

    if (ethereum) {
      const provider = new ethers.providers.Web3Provider(ethereum);
      const signer = provider.getSigner();
      const landPortalContract = new ethers.Contract(
        contractAddress,
        contractABI,
        signer
      );

      let tx = await landPortalContract.writeContent(landId, content);
      //console.log(await landPortalContract.getQuestionById(questionId));
      // let tx = await landPortalContract.chooseAnswer(questionId, winnerAddress);
      await tx.wait();
      return true;
    } else {
      console.log("Ethereum object doesn't exist!");
      return false;
    }
  } catch (error) {
    console.error(error);
  }
};

const mintOneLand = async () => {
  try {
    console.log("start");
    const { ethereum } = window;

    if (ethereum) {
      const provider = new ethers.providers.Web3Provider(ethereum);
      const signer = provider.getSigner();
      const landPortalContract = new ethers.Contract(
        contractAddress,
        contractABI,
        signer
      );
      let tx = await landPortalContract.mint();
      await tx.wait();
      return true;
    } else {
      console.log("Ethereum object doesn't exist!");
      return false;
    }
  } catch (error) {
    console.error(error);
  }
};

const contractService = {
  getContentByLandId,
  writeOnNewsLand,
  mintOneLand,
};

export default contractService;
