import { useEffect, useState } from "react";
import { Block, landProps } from "types/landProps";
import contractService from "services/contract.service";

export const calculateBlockId = (coords: any) => {
  return Math.trunc(coords.x / 30) + Math.trunc(coords.y / 30) * 30;
};

export const convertIdToStartXY = (id: number) => {
  const startX = (id % 30) * 30 + 1;
  const startY = Math.trunc(id / 30) * 30 + 1;
  return { startX, startY };
};

export const calculateStartXY = (value: number) => {
  return value - (value % 30) + 1;
};

export function convertCoordToBlock(coords: any) {
  const id = calculateBlockId(coords);
  const block: Block = {
    id: id,
    content: "",
    attribute: {
      startX: calculateStartXY(coords.x),
      startY: calculateStartXY(coords.y),
      width: 28,
      height: 28,
    },
  };
  return block;
}

export default function useNewsLand(canvasRef: any) {
  const [contextState, setContextState] =
    useState<CanvasRenderingContext2D | null>();
  const lands: landProps[] = [];

  // ---- functions, which use contextState. ---
  function clickCanvasWithCoords(event: any) {
    if (!contextState) {
      console.error("canvas is not exist");
      return;
    }
    const x = event.pageX - contextState.canvas.offsetLeft;
    const y = event.pageY - contextState.canvas.offsetTop;

    const clickedBlock: Block = convertCoordToBlock({ x, y });

    const startX = calculateStartXY(x);
    const startY = calculateStartXY(y);
    let width = 28;
    let height = 28;

    console.log("(" + x + ", " + y + ") is clicked.");
    const clickCoords = new Path2D();
    clickCoords.rect(startX, startY, width, height);
    contextState.fillStyle = "blue";
    contextState.fill(clickCoords);
    contextState.fillStyle = "black";

    lands.push({ startX, startY, width, height });

    return clickedBlock;
  }

  function clearLastClickBlock(block: Block) {
    contextState!.clearRect(
      block.attribute.startX,
      block.attribute.startY,
      28,
      28
    );
  }

  async function clickedMintButton() {
    try {
      const isSuccess = await contractService.mintOneLand();
      if (isSuccess) {
        window.location.reload();
      } else {
        //모달 님 민팅 실패
        alert("Minting failed, please try again");
      }
    } catch (e) {}
  }

  async function getAllcontents() {
    for (let id = 0; id < 900; id++) {
      try {
        const content: string = await contractService.getContentByLandId(id);
        // 위험 요소 있음
        if (content) {
          const { startX, startY } = convertIdToStartXY(id);
          contextState!.fillText(content, startX, startY);
        }
      } catch (err) {
        break;
      }
    }
  }

  function onMouseMove(event: any) {
    let x = event.pageX;
    let y = event.pageY;
    console.log("Coordinates: (" + x + "," + y + ")");
  }

  function _drawGridLine(context: CanvasRenderingContext2D, y: number) {
    context.moveTo(0, y);
    context.lineTo(900, y);
    context.moveTo(y, 0);
    context.lineTo(y, 900);
  }

  useEffect(() => {
    const canvas = canvasRef.current!;
    const context: CanvasRenderingContext2D = canvas.getContext("2d");
    setContextState(context);

    if (!canvas || !context) {
      console.error("canvas is not exist");
      return;
    }

    // drawing canvas
    context.translate(0.5, 0.5);
    context.lineWidth = 1;

    context.beginPath();
    for (let y = 0; y <= 900; y += 30) {
      _drawGridLine(context, y);
    }

    context.stroke();

    getAllcontents();
  }, []);

  return {
    onMouseMove,
    clickCanvasWithCoords,
    clearLastClickBlock,
    calculateBlockId,
    clickedMintButton,
  };
}
